<template>
    <button class="button03">{{ info }}</button>
</template>

<script>
export default {
    name: "Button03",
    props:{
        init:{
            default:'默认按钮',  // 不传参的默认值
            type:String,  // 类型
        }
    },
    data() {
        return {
            info: this.init
        }
    }
}
</script>

<style lang="less" scoped>
.button03{
    background-color: transparent;
    border: 1px solid rgba(0, 0, 0, 0.1);
    color: rgba(0, 0, 0, 0.5);
}

.button03:hover{
    background-color: transparent;
    border: 1px solid @main-color-red;
    color: @main-color-red;
}

</style>