<template>
    <nav class="site-nav" :class="{see:opacityStyle}">
        <div class="site-nav-left">
            <div class="site-nav-logo-box">
                <img src="https://www.minecraft.net/content/dam/games/minecraft/logos/logo-minecraft.svg" alt=""
                     class="site-nav-logo">
            </div>
        </div>

        <div class="site-nav-right">
            <ul class="site-nav-list">
                <li><a href="https://mc.data07.cn/">首页</a></li>
                <li><a href="javascript:void (0);" @click="handleScrollTo('ServerList')">服务器</a></li>
                <li><a href="javascript:void (0);" @click="handleScrollTo('moneyList')">玩家贡献</a></li>
                <li><a href="https://mc.minebbs.com/">游戏下载</a></li>
                <li><a href="#">黑名单</a></li>
            </ul>
        </div>
    </nav>
</template>

<script>
export default {
    name: "HeaderView",
    data() {
        return {
            opacityStyle: false
        }
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
    },
    methods: {
        handleScroll() {
            let scrollTop =
                window.pageYOffset ||
                document.documentElement.scrollTop ||
                document.body.scrollTop;
            // let offsetTop = document.querySelector(".header").offsetTop;
            //设置背景颜色的透明读
            if (scrollTop >= 500-80) {
                this.opacityStyle = true;
            } else {
                this.opacityStyle = false;
            }
        },
        handleScrollTo(id) {
            var element = document.getElementById(id);
            element.scrollIntoView({ behavior: "smooth",block: 'center'}); // 参数 false 代表 Bottom
        }
    },
    beforeDestroy() {
        window.removeEventListener("scroll", this.handleScroll);
    },
}
</script>

<style lang="less" scoped>


.site-nav {
  position: fixed;
  top: 0;
  z-index: 100;
  width: 100%;
  box-sizing: border-box;
  height: 68px;

  display: flex;
  flex-flow: row;
  justify-content: start;
  align-items: center;
  padding: 0 50px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.2), rgba(255, 255, 255, 0));
  transition: all 0.3s;

  .site-nav-list li a {
    color: white;
  }
}


.site-nav-list {
  display: flex;
  flex: 1;
  height: 100%;
  flex-flow: row;
  justify-content: start;
  align-items: center;
}

.site-nav-left {
  height: 100%;
  flex: 1;
  display: flex;
  justify-content: start;
  align-items: center;
}

.site-nav-right {
  height: 100%;
  display: flex;
  flex-flow: row-reverse;
  justify-content: start;
  align-items: center;
}

.site-nav-logo-box {
  height: 40%;
}

.site-nav-logo {
  height: 100%;
  margin-right: 10px;
}

.site-nav-list li {
  line-height: 68px;
  padding: 0 8px;
  margin-left: 16px;
}

.site-nav-list li a {
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
}

.see {
  background: #fff;
  box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.10);

  .site-nav-list li a {
    color: rgba(0, 0, 0, 0.65);
  }

  .site-nav-list li a:hover {
    color: @main-color-red;
  }
}


@media screen and (max-width: 679px) {
  .site-nav-list{
    opacity: 0;
  }
}
</style>