<template>
    <button class="button01">{{ info }}</button>
</template>

<script>
export default {
    name: "Button01",
    props: {
        init: {
            default: '重要按钮',
            type: String,
        }
    },
    data() {
        return {
            info: this.init
        }
    }
}
</script>

<style lang="less" scoped>

.button01 {
  border: 1px solid @main-color-red;
  background-color: @main-color-red;
  color: white;
}

.button01:hover {
  border: 1px solid @main-color-red-select;
  background-color: @main-color-red-select;
  color: white;
}
</style>