<template>
    <button class="button02">{{ info }}</button>
</template>

<script>
export default {
    name: "Button02",
    props:{
        init:{
            default:'一般按钮',  // 不传参的默认值
            type:String,  // 类型
        }
    },
    data() {
        return {
            info: this.init
        }
    }
}
</script>

<style lang="less" scoped>
.button02{

    background-color: transparent;

    border: 1px solid @main-color-red;
    color: @main-color-red;
}

.button02:hover{
    background-color: #e94c88;
    color: white;
}

</style>